/* ------------------------------------------------------------------------- */
/* G Review Grid 1 */
/* ------------------------------------------------------------------------- */

#g-review {
  &.layout_style-1 {
    .g-review {
      padding: 30px;
      border-radius: 16px;
      opacity: 1;
      box-shadow: 0 5px 10px rgba(0,0,0,0.16);

      .gr-inner-header {
        img {
          &.gr-profile {
            margin: 0 20px 10px 0;
            width: 50px;
            height: 50px;
          }

          &.gr-google {
            width: 20px;
            height: 20px;
          }
        }
        p {
          position: relative;
          top: -1px;
          flex-basis: calc(100% - 75px);

          a {
            padding-right: 20px;
          }
        }
        .gr-stars {
          display: flex;
          align-items: end;
          flex-wrap: wrap;
          .stars-wrapper {
            width: 115px;

            img {
              margin-right: 5px !important;
            }
          }
          .time {
            margin-left: 5px;
            width: 98px;
            display: inline-block;
          }
        }
      }
      .gr-inner-body {
        max-height: 76px;
        overflow: auto;

        p {
          margin: 0 0 4px;
          text-align: left;
          font-size: 14px;
          line-height: 1.36;
          letter-spacing: 0;
          color: rgba(0,0,0,0.6);
        }
      }
    }
    .swiper-button-next,
    .swiper-button-prev {
      box-shadow: 0 3px 6px rgba(0,0,0,0.16);
    }
  }
}


